:root {
  --color-primero:#7d3c98;
  --color-primeroDark:#8e44ad;
  --color-segundo:#008080;
  --color-tercero:#ff0;
  --color-cuarto: #750D37;
  --blanco: #fffffe;
  --negro: #151516;
  --color-nota: #b8bfc6;
  --bg-nota: #363b40;
  --scrollOne:#48D1CC;
  --scrollTwo:#4682B4;
  --fuenteHeading: "Original Surfer", cursive;
  --fuenteText: "Handlee", cursive;
}

.dark {
  background-color: var(--negro);
}
.dark p,
.dark a {
  color: var(--blanco);
}
.dark .icons {
  filter: invert(100%);
}
.dark .barra {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(255, 255, 255, 0.5);
}
.dark .nav__link:hover {
  background-color: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(255, 255, 255, 0.5);
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: var(--fuenteText);
  transition: 0.5s;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1,
h2,
p,
ul {
  padding: 0;
  margin: 0;
}

/* =====BARRA LATERAL MENU===== */
.barra {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 5rem;
  height: 100vh;
  color: var(--negro);
  padding: 0.5rem;
  transition: 0.5s;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(0, 0, 0, 0.5);
}
@media screen and (min-width: 1024px) {
  .barra {
    width: 6rem;
  }
}

.barra__nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.barra__nav-brand {
  visibility: hidden;
}
@media screen and (min-width: 1024px) {
  .barra__nav-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
    visibility: visible;
  }
}

.nav__toggle {
  width: 5rem;
  padding: 0.5rem;
  cursor: pointer;
}

.nav__logo {
  color: var(--negro);
  font-weight: 600;
  font-size: 1.2rem;
}

.nav__link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 2rem;
  padding: 1rem;
  color: var(--negro);
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
  border: 0.1rem solid transparent;
}
.nav__link:hover {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 0.1rem solid rgba(0, 0, 0, 0.5);
}

.nav__icon {
  width: 2rem;
}
@media screen and (min-width: 1024px) {
  .nav__icon {
    width: 3rem;
  }
}

.nav__name {
  font-size: 1.5rem;
}

.expander {
  width: 17rem;
}